.footer{

display: flex;

align-items: center;
width: 30%;
margin: auto;
justify-content: space-between;

margin-top: 80px;

}

.footerdiv{

background-color:#027bff  ;

/* border: 1px solid red; */
height: auto;
padding-top: 50px;
}


.telephone{

display: flex;
text-align: center;
justify-content: center;


}



.telephoneicon{

  display: flex;
  text-align: center;
  justify-content: center;

  margin-right: 5px;
  
  
  }
  







@media (min-width: 481px) and (max-width: 768px) {
    .footer {
      width: 100%;
      display: flex;
      display: grid;
      grid-template-columns: repeat(2,1fr);

     
    }
    
  }

  @media (min-width: 0px ) and (max-width: 480px) {
    .footer {
        width: 100%;
      display: flex;
      display: grid;
      grid-template-columns: repeat(1,1fr);
      margin: auto;
      

     
    }
    
  }
