

.portfolio{

display: flex;
justify-content: center;
align-items: center;
display: grid;
grid-template-columns: repeat(3,30%);
gap: 30px;

height: auto;


}

.portfolioimage{

width: 95%;
height: 400px;

}


.portfoliodiv{
  box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
 
padding: 5px;
padding-bottom: 10px;
border-radius: 5px;

}


@media (min-width: 481px) and (max-width: 768px) {
    .portfolio {
      width: 100%;
      display: flex;
      display: grid;
      grid-template-columns: repeat(2,1fr);

     
    }
    
  }

  @media (min-width: 0px ) and (max-width: 480px) {
   .portfolio {
        width: 100%;
      display: flex;
      display: grid;
      grid-template-columns: repeat(1,1fr);
      margin: auto;
      

     
    }
    
  }


