@media (min-width: 481px) and (max-width: 768px) {
    .skills {
      width: 100%;
      display: flex;
      display: grid;
      grid-template-columns: repeat(3,1fr);

     
    }
    
  }

  @media (min-width: 0px ) and (max-width: 480px) {
    .skills {
        width: 100%;
      display: flex;
      display: grid;
      grid-template-columns: repeat(1,1fr);
      margin: auto;
      

     
    }
    
  }