
.header{

    display: flex;
   
    align-items: center;
    justify-content: flex-end;
    gap: 60px;
    background-color: #f2f6ff;
    box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
 padding-left: 20px;
 height: auto;
 position: fixed; 
 top: 0; 
 width: 100%;
 z-index: 10;


}
.name{

padding-right: 600px;
font-weight: bold;
color: blueviolet;
font-size: 20px;

}






@media (min-width: 481px) and (max-width: 768px) {
    .header {
      width: 100%;
      display: flex;
      display: grid;
      grid-template-columns: repeat(2,1fr);

     
    }
    
  }

  @media (min-width: 0px ) and (max-width: 480px) {
    .header {
        width: 100%;
      display: flex;
      /* display: grid;
      grid-template-columns: repeat(1,1fr); */
      margin: auto;
      height: 60px;
      

     
    }
    
  }

